:root{
    --white: rgb(248, 249, 241);
    --border: 2px solid var(--white)
}

body{
    background-image: url('../images/number_Background_1.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
  }
  
.container{
    /* background-color: red; */
    /* border: var(--border); */
    border-radius: 5px;
    min-width: 60vw;
    padding: 40px 20px;
    height: 300px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    align-items: center;
    justify-content: center;
    color: var(--white);
}

.one
{
    /* background-color: white;
    color: black; */
    width: 60vw;
    height: 30px;
    line-height: 30px;
}

input{
    width: 40vw;
    height: 50px;
    border-radius: 5px;
    padding: 0 15px;
    background-color: transparent;
    border: var(--border);
    color: var(--white);
    font-size: 1.5rem;
}



.result{
    min-width: 50vw;
    border: 2px solid ;
    /* margin-top: 20px; */
    height: 25px;
    line-height: 30px;
    font-weight: bold;
    padding: 20px;
    border-radius: 5px;
    background-color: var(--white);
    color: black;
}

.resultparent
{
    border: 2px solid ;
    border-radius: 5px;
    padding: 10px;
}